
.task {
  &__item {
    position: relative;
    padding-left: 23px;
    line-height: 1.4;
    color: $textcolor;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 10px;
      border-radius: 50%;
      width: 6px;
      height: 6px;
      background-color: $anakiwa;
    }

    &:not(:last-child) {
      margin-bottom: 12px;

      @include mw3 {
        margin-bottom: 8px;
      }

      @include mw4 {
        margin-bottom: 5px;
      }
    }

    @include mw4 {
      padding-left: 18px;
    }
  }
}

.task-number {
  counter-reset: my-counter;

  &__chap {
    display: block;
    margin-bottom: 7px;
  }

  &__item {
    position: relative;
    padding-left: 20px;
    line-height: 1.45;

    &::before {
      counter-increment: my-counter;
      content: counter(my-counter) '.';
      position: absolute;
      left: 0;
      top: 0;
      font-weight: 500;
      color: currentColor;
    }

    &:not(:last-child) {
      margin-bottom: 18px;

      @include mw3 {
        margin-bottom: 12px;
      }

      @include mw4 {
        margin-bottom: 10px;
      }
    }
  }
}